import React, { FunctionComponent, ReactNode } from 'react'
import styled from '@emotion/styled'
import { Link } from 'gatsby'

type CategoryItemProps = {
  active: boolean
}

// 여기서는 두 가지의 타입을 선언했는데요, 첫 번째는 CategoryItem 컴포넌트에서 사용할 Props, 두 번째는 Link 컴포넌트에 전달해주기 위한 Props입니다.

// 원래는 GatsbyLinkProps에 active 속성도 존재하지만 중복되는 문제도 있고, CategoryItem 컴포넌트에서는 그 외의 Props는 사용하지 않기 때문에 분리하였습니다.

// 따라서 & 연산자를 통해 Intersection 타입을 정의하여 두 타입을 하나로 합쳐주었습니다.

// 그럼 타입을 정의해주었으니, Styled Component 부분에 적용해봅시다.
type GatsbyLinkProps = {
  children: ReactNode
  className?: string
  to: string
} & CategoryItemProps

export type CategoryListProps = {
  selectedCategory: string
  categoryList: {
    [key: string]: number
  }
}

const CategoryListWrapper = styled.div`
  position: relative;
  width: 30%;
  padding-right: 15px;

  @media (max-width: 1079px) {
    display: none;
  }
`

const CategoryBox = styled.div`
  color: rgba(0, 0, 0, 0.87);
  background-color: white;
  border: 1px solid rgba(0, 0, 0, 0.12);
  border-radius: 6px;

  position: sticky;
  top: 60px;

  /* 스크롤 가능하게 설정 */
  max-height: calc(100vh - 60px); /* 화면 크기에서 sticky top 값 제외 */
  overflow-y: auto; /* 세로 스크롤 활성화 */
  padding: 10px; /* 내용 여백 */
`
const CategoryTitle = styled.h2`
  color: rgba(0, 0, 0, 0.6);
  font-weight: 500;
  letter-spacing: 1px;
  font-size: 36px;
  margin-bottom: 10px;
  margin-top: 5px;
  margin-left: 10px;
`

const CategoryItemWrapper = styled.ul`
  position: relative;
  margin-left: 10px;
  padding: 4px;
`

const LinkWrapper = styled.li`
  font-size: 20px;
  color: rgba(0, 0, 0, 0.6);
  position: relative;
  transition: 0.5s;
  position: relative;
  list-style: none;
  margin: 4px 0;
  border-left: 2px solid rgba(4, 120, 87, 0.8);
  cursor: pointer;

  &:hover {
    left: 10px;
  }

  &:before {
    content: '';
    position: absolute;
    width: 94%;
    height: 100%;
    background: rgba(4, 120, 87, 0.2);
    transform: scaleX(0);
    transform-origin: left;
    transition: 0.5s;
    // transform: scaleX(0);
    // transform-origin: right;
    // transition: transform 0.5s ease-in-out;
  }

  &:hover:before {
    transition: 0.5s;
    transform: scaleX(1);
    // transform: scaleX(1);
    // transform-origin: left;
  }

  &:hover div {
    font-size: 22px;
    color: black;
  }
`

const CategoryName = styled.div`
  transition: 0.5s;
  color: rgba(0, 0, 0, 0.6);
  padding-left: 10px;
`

const CategoryCount = styled.div`
  top: 50%;
  right: 30px;
  position: absolute;
  transform: translateY(-50%);
  padding-top: 1px;
  padding-left: 5px;
  padding-right: 5px;
  padding-bottom: 1px;

  color: rgba(0, 0, 0, 0.6);
  background: rgba(0, 0, 0, 0.1);
  border-radius: 6px;
`

// styled 함수 내 Props 받는 데이터
// active : 현재 선택된 카테고리인지 확인하기 위한 Props
// children : 컴포넌트 내부의 요소들
// className : Styled Component를 통해 정의한 스타일을 적용하기 위한 클래스명
// to : Link 컴포넌트에 전달하기 위한 경로
const CategoryItem = styled(({ active, ...props }: GatsbyLinkProps) => (
  <Link {...props} />
))<CategoryItemProps>`
  position: relative;
`
const CategoryList: FunctionComponent<CategoryListProps> = function ({
  selectedCategory,
  categoryList,
}) {
  return (
    <CategoryListWrapper>
      <CategoryBox>
        <CategoryTitle> Category </CategoryTitle>

        {Object.entries(categoryList).map(([name, count]) => (
          <CategoryItem
            to={`/?category=${name}`}
            active={name === selectedCategory}
            key={name}
          >
            <CategoryItemWrapper>
              <LinkWrapper>
                <CategoryName>{name}</CategoryName>
              </LinkWrapper>
              <CategoryCount>{count}</CategoryCount>
            </CategoryItemWrapper>
          </CategoryItem>
        ))}
      </CategoryBox>
    </CategoryListWrapper>
  )
}

export default CategoryList
