const PostItemContainer = styled.div`
  border-radius: 10px;
  transition: all 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  cursor: pointer;
  border: 1px solid rgba(0, 0, 0, 0.12);
  max-width: 90%;

  @media (min-width: 768px) {
    max-width: 100%;
    height: 200px;
  }
  &:hover {
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
    transform: scale(1.01);
  }
`

const PostItemContents = styled.div`
  overflow: hidden;
  border-radius: 0.5rem;
  background-color: #ffffff;
  height: 100%;

  @media (min-width: 768px) {
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    width: 100%;
  }
`

const ThumbnailImage = styled(GatsbyImage)`
  height: 14rem;
  background-position: bottom;
  background-size: cover;

  @media (min-width: 768px) {
    display: flex;
    width: 20%;
    flex-shrink: 1;
    height: 100%;
  }
`

const PostItemContent = styled.div`
  padding: 2rem;

  @media (min-width: 768) {
    display: flex;
    width: 80%;
    flex-shrink: 1;
  }
`

const PostTitleSummaryWrapper = styled.div``

const Title = styled.div`
  font-size: 1.25rem;
  line-height: 1.75rem;
  font-weight: 700;

  @media (min-width: 768px) {
    display: flex;
    font-size: 1.5rem;
    line-height: 2rem;
  }
`

const TitleSpan = styled.span`
  line-height: 1.4;
  color: #222022;
  box-decoration-break: clone;
  background-image: linear-gradient(90deg, #ffef7e, #b7f9e9);
  background-size: 100% 42%;
  background-repeat: no-repeat;
  background-position: 0 85%;
  padding: 0 2px;
  margin-left: -4px;
`

const Date = styled.div`
  font-size: 14px;
  font-weight: 400;
  opacity: 0.7;
`

const Summary = styled.div`
  color: #374151;

  @media (min-width: 1029px) {
    display: flex;
    width: 38rem;
    font-size: 16px;
  }

  @media (min-width: 768px) {
    display: flex;
    width: 32rem;
    font-size: 16px;
  }
`

const Tag = styled.div`
  display: flex;
  flex-wrap: wrap;
  margin-top: 10px;
  margin: 10px -5px;
`

const TagItem = styled.div`
  margin: 2.5px 5px;
  padding: 3px 5px;
  border-radius: 3px;
  background: rgba(4, 120, 87, 0.8);
  font-size: 14px;
  font-weight: 700;
  color: white;
`

const PostItem: FunctionComponent<PostItemProps> = function ({
  title,
  date,
  category,
  summary,
  tags,
  thumbnail: {
    childImageSharp: { gatsbyImageData },
  },
  link,
}) {
  return (
    <PostItemWrapper to={link}>
      <PostItemContainer>
        <PostItemContents>
          {/* <ThumbnailWrapper> */}
          <ThumbnailImage image={gatsbyImageData} alt="Post Item Image" />
          {/* </ThumbnailWrapper> */}
          <PostItemContent>
            <PostTitleSummaryWrapper>
              <Title>
                <TitleSpan>{title}</TitleSpan>
              </Title>
              <Date>{date}</Date>
              <Summary>{summary}</Summary>
              <Tag>
                {tags.map(item => (
                  <TagItem key={item}>{item}</TagItem>
                ))}
              </Tag>
            </PostTitleSummaryWrapper>
          </PostItemContent>
        </PostItemContents>
      </PostItemContainer>
    </PostItemWrapper>
  )
}

export default PostItem
